import * as React from 'react';
import {
  TableResizeController,
  TableResizeControlPoint,
  TableResizeControlLines,
  TableResizeControlBottomLine,
  TableResizeControlRightLine,
} from './styled';

interface Props {
  visible: boolean;
  onMouseDown: React.MouseEventHandler;
}

export default React.forwardRef((props: Props, ref: any) => {
  const { visible, onMouseDown } = props;
  const { tableResizeControllerRef, tableResizeControlLinesRef } = ref;
  return (
    <TableResizeController
      role="resizer"
      data-testid="table-corner-resizer"
      ref={tableResizeControllerRef}
      onMouseDown={onMouseDown}
      style={{ display: visible ? 'block' : 'none' }}
    >
      <TableResizeControlPoint />
      <TableResizeControlLines ref={tableResizeControlLinesRef}>
        <TableResizeControlBottomLine />
        <TableResizeControlRightLine />
      </TableResizeControlLines>
    </TableResizeController>
  );
});
